<template>
  <div>
    <div v-if="file">
      <h1 class="subtitle title--no-sp is-3">{{ file.name }}</h1>
      <span class="subtitle is-6">
        {{ file.downloadCount }}
        {{ $t("file.downloads") }} / {{ file.visitCount }} {{ $t("file.visits") }}
      </span>
      <div class="mt-6">
        <Adsense data-ad-client="ca-pub-4679085340013866" data-ad-slot="6511749031"></Adsense>
        <div>
          <a
            :href="`${file.url}`"
            class="button is-danger"
            :class="{ 'pointer-events-none': downloaded }"
            target="_blank"
            @click="onFileDownload"
            :disabled="downloaded"
          >{{ $t("file.downloadFile") }}</a>
        </div>
        <Adsense data-ad-client="ca-pub-4679085340013866" data-ad-slot="6511749031"></Adsense>
      </div>
    </div>
    <div class="mt-6">
      <article class="message max-w-md mr-auto ml-auto">
        <div class="message-header">
          <p
            class="mb-0"
          >Top cloud providers 2019: AWS, Microsoft Azure, Google Cloud;
          IBM makes hybrid move; Salesforce dominates SaaS</p>
        </div>
        <div class="message-body">
          The top cloud providers for 2019 have maintained their positions,
          but the themes, strategies,
          and approaches to the market are all in flux. The infrastructure-as-a-service wars have
          been largely decided, with the spoils going to Amazon Web Services, Microsoft Azure, and
          Google Cloud Platform, but new technologies such as artificial intelligence and machine
          learning have opened the field up to other players.
          Meanwhile, the cloud computing market in 2019 will have a decidedly multi-cloud spin,
          as the
          hybrid shift by players such as IBM, which is acquiring Red Hat, could change the
          landscape. This year's edition of the top cloud computing providers also features
          software-as-a-service giants that will increasingly run more of your enterprise's
          operations via expansion.
          One thing to note about the cloud in 2019 is that the market isn't zero sum. Cloud
          computing
          is driving IT spending overall. For instance, Gartner predicts that 2019 global IT
          spending will increase 3.2 percent to $3.76 trillion with as-a-service models
          fueling everything from data center spending to enterprise software.

        </div>
      </article>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import EventBus from '@/eventBus';

export default {
  data: () => ({
    file: null,
    title: '',
    downloaded: false,
  }),
  mounted() {
    axios
      .get(`/api/files/${this.$route.params.id}`)
      .then(({ data: file }) => {
        this.file = file;
        this.file.visitCount += 1;
        this.title = `${file.name} - `;
      })
      .catch((err) => {
        console.log(err);

        if (err.response.status === 500) {
          return EventBus.$emit(
            'error',
            'Server Error: Unable to handle the request now. Please try again later.',
          );
        }

        return this.$router.push('/404');
      });
  },
  methods: {
    onFileDownload() {
      this.file.downloadCount += 1;
      this.downloaded = true;

      axios.post(`/api//download/${this.file.encodedName}`);
    },
  },
  metaInfo() {
    return {
      title: this.title,
    };
  },
};
</script>
